<!-- 文件列表 -->
<template>
  <div class="chat-file-list">
    <d-card class="list-card" :header-style="{ padding: '5px 20px' }" ref="list">
      <file-list-head slot="header" @sort="sortList" :sorting="sorting" :sortRule="sortRule"></file-list-head>
      <div class="file-list">
        <d-card class="file-list-item" shadow="hover" v-for="(item, index) in list" :key="index" :body-style="{ padding: '5px 0px' }" :radius-style="{borderRadius: '0px'}">
          <file-list-item :file="item"></file-list-item>
        </d-card>
        <div class="empty-data" v-if="getFileList.length === 0">
          <img class="empty-data-img" :src="noData" :alt="$t('file.emptyList')">
        </div>
      </div>
    </d-card>
  </div>
</template>

<script>
import _  from 'lodash';
import { mapGetters } from 'vuex';
import FileListHead from './FileListHead';
import FileListItem from './FileListItem';
import FileListMixin from './FileListMixin';
import FileListItemSkeleton from './FileListItemSkeleton';
export default {
  name: 'chat-file-list',
  mixins: [ FileListMixin ],
  components: {
    FileListHead,
    FileListItem,
    FileListItemSkeleton
  },
  data() {
    return {
      initLength: 25,
      sorting: 4, // 排序项的index
      sortRule: 0, // 排序规则 1升0降-1初始化,
      noData: require('assets/images/nocontent.png')
    }
  },
  computed: {
    ...mapGetters(['file_list', 'search_list', 'members_range']),
    getFileList() {
      return this.filterList(_.orderBy(this.file_list,
       [ this.sortTypeObject[this.sorting] ? this.sortTypeObject[this.sorting] : 'time'],
       [ this.sortRuleObject[this.sortRule] ? this.sortRuleObject[this.sortRule] : 'desc' ]).slice(0, this.initLength));
    },
    // 搜索文件
    searchFileList: {
      get() {
        if (this.search_list !== null) {
          return this.filterList(_.orderBy(this.search_list,
            [ this.sortTypeObject[this.sorting] ? this.sortTypeObject[this.sorting] : 'time'],
            [ this.sortRuleObject[this.sortRule] ? this.sortRuleObject[this.sortRule] : 'desc' ]).slice(0, this.initLength));
        }
        return null;
      },
      set() {
        this.initLength = 25;
      }
    },
    list() {
      return this.searchFileList !== null ? this.searchFileList : this.getFileList;
    }
  },
  mounted() {
    this.$nextTick(() => {
      const cardBody = document.querySelector('.list-card>.d-card__body')
      cardBody.addEventListener('scroll', (e) => {
        e.stopPropagation();
        this.loadMoreData(cardBody);
      })
    })
  },
  methods: {
    // 滑动加载更多
    loadMoreData(el) {
      this.$nextTick(() => {
        if (this.isScrollToBottom(el)){
          console.log('到底了');
          let list = this.search_list !== null ? this.search_list : this.file_list;
          if (this.initLength < list.length) {
            //是否有更多数据
            console.log('加载更多数据');
            if (list.length - this.initLength < 25) {
              this.initLength = list.length;
              console.log('已展示全部数据');
            } else {
              this.initLength += 25;
              console.log('已增加展示数量');
            }
          }
        }
      })
    },
    // 排序函数
    sortList(rule, type) {
      this.sorting = type;
      this.sortRule = rule;
    },
    // 过滤函数
    filterList(array) {
      let filterResult = Object.keys(this.members_range)[0];
      if (filterResult === undefined) {
        return array;
      }
      // 群成员
      if (filterResult === 'member') {
        return _.filter(array, e => Object.keys(this.members_range.member).includes(e.userid) && this.members_range.member[e.userid].groupId === e.targetid);
      }
      // 群组
      if (filterResult === 'group') {
        return _.filter(array, e => Object.keys(this.members_range.group).includes(e.targetid));
      }
      // 联系人
      if (filterResult === 'contact') {
        return _.filter(array, e => Object.keys(this.members_range.contact).includes(e.userid));        
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.chat-file-list{
  width: calc(100% - 80px);
  height: 100%;
  .list-card{
    height: 100%;
    &>/deep/ .d-card__body{
      padding: 10px 20px;
      box-sizing: border-box;
      height: calc(100% - 32px);
      overflow: hidden;
      &:hover{
        overflow: overlay;
      }
    } 
  }
  .file-list{
    height: 100%;
    // &:hover {
    //   overflow: overlay;
    // }
    .empty-data{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      .empty-data-img{
        width: 20%;
      }
    }
  }
  .file-list-item{
    box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.1),-3px 0px 6px rgba(0, 0, 0, 0.1);
    border-color:	#F8F8F8;
    margin-bottom: 10px;
    &:hover{
      box-shadow: 6px 6px 12px rgba(0, 0, 0, 0.1),-6px 0px 12px rgba(0, 0, 0, 0.1);
    }
  }
}
</style>