<template>
  <v-modal class="filter-modal" @close="cancelMembersList">
    <div class="header-input-box" slot="header-top">
      <d-input
        class="header-search-input"
        v-model="searchMember"
        :placeholder="$t('file.searchGroupMembers')"
        prefixIcon="d-icon-search"
        outlined
        clearable
        size="small"
      ></d-input>
      <p class="search-select">{{$t('file.membersList')}}</p>
    </div>
    <div class="members">
      <div v-for="(element,index) in membersList" :key="index" class="members-list-item">
        <d-avatar size="30" :src='element[1].thumbAvatar'></d-avatar>
        <span class="group-remark">{{element[1].remark}}</span>
        <d-checkbox size="medium" class="checkbox-right"  @input='selectMembers(element[1])' :value='groupSelected(element[0])'></d-checkbox>
      </div>
    </div>
    <div class="operation" slot="footer-bottom">
      <d-button size="mini" class="operation-button" @click="confirm">{{$t('file.confirm')}}</d-button>
      <d-button size="mini" class="operation-button" type="danger" @click="cancelMembersList">{{$t('file.back')}}</d-button>
    </div>

  </v-modal>
</template>
<script>
import _  from 'lodash';
import { mapGetters } from 'vuex';
import { converseFullPinYin } from 'utils/pinyin.js'
import FileListMixin from '../File/FileList';
export default {
  name: 'group-members-list',
  mixins: [ FileListMixin ],
  props:['groupId','returnMemberListValue'],
  data() {
    return {
      searchMember: '',
      showModal:false,
      selectedGroupMember:{},
      groupMembersListObj: {},
      initLength: 10,
      membersinfo:{}
    };
  },
  mounted() {
    console.log('returnMemberListValue',this.returnMemberListValue)
    this.$Service.group.getMemberList({ groupId: this.groupId }, resp => {
      console.log("resp.members", resp);
      this.membersinfo = Object.assign({}, resp.members)
      this.$store.commit('SET_MEMBERSLIST', resp.members);
      this.$Service.log(`拉取群成员列表：：：${JSON.stringify(resp.members)}`);
    });
    this.$nextTick(() => {
      const cardBody = document.querySelector('.members');
      cardBody.addEventListener('scroll', (e) => {
        e.stopPropagation();
        this.loadMoreData(cardBody);
      })
    });
    // if (this.members_range.hasOwnProperty('member')) {
    if (this.returnMemberListValue.hasOwnProperty('member')) {
      this.selectedGroupMember = Object.assign({}, this.returnMemberListValue.member);
      this.$set(this.groupMembersListObj, 'member', this.selectedGroupMember.member);
      // this.$emit('selectMembers', {'member': this.selectedGroupMember});
    }
  },
  computed: {
    ...mapGetters([ 'members_list', 'members_range' ]),
    membersList() {
      // console.log('群成员列表:',this.members_list);
       console.log('群成员列表:',this.membersinfo);
      if (this.searchMember.trim() === '') {
        return _.orderBy(
          //  this.members_list,
           this.membersinfo,
          val => {
            // 这里是  pingyin  不是 pinyin
            return converseFullPinYin(val[1].pingyin.trim());
          },
          ['asc']
        ).slice(0, this.initLength)
      }
      return _.filter(this.membersinfo, e => e[1].remark.indexOf(this.searchMember) >= 0);
    }
  },
  methods: {
    // 滑动加载更多
    loadMoreData(el) {
      this.$nextTick(() => {
        if (this.isScrollToBottom(el)){
          console.log('到底了');
          if (this.initLength < this.members_list.length) {
            console.log('加载更多数据');
            if (this.members_list.length - this.initLength < 10) {
              this.initLength = this.members_list.length;
              console.log('已展示全部数据');
            } else {
              this.initLength += 10;
              console.log('已增加展示数量');
            }
          }
        }
      })
    },
    selectMembers(value) {
      console.log('群成员属性',value);
      if(this.selectedGroupMember.hasOwnProperty(value.id)) {
        //如果当前选择的列表中已经有该成员，则删除它
        this.$delete(this.selectedGroupMember, value.id);
      }else{
        this.$set(this.selectedGroupMember, value.id, value);
      }
      console.log("this.selectedGroupMember:", this.selectedGroupMember);
      this.groupMembersListObj = {
        'member': this.selectedGroupMember
      }
      console.log("选中的人员数组:", this.groupMembersListObj);
    },
    groupSelected(id) { // 接收到scopeFilter页面的返回值
      // if (this.members_range.hasOwnProperty('member')) {
      if (this.returnMemberListValue.hasOwnProperty('member')) {
        // let groupMembersKeys = Object.keys(this.members_range.member);
        let groupMembersKeys = Object.keys(this.returnMemberListValue.member);
        console.log('returnMemberListValue',this.returnMemberListValue)
        return groupMembersKeys.includes(id);
      }
    },
    cancelMembersList() {
      this.$emit("closePage");
    },
    confirm() {
      let selectedGroupObj = (() => {
        for(var key in this.groupMembersListObj){
          return this.groupMembersListObj[key];
        }
      })();
      if (Object.keys(selectedGroupObj).length > 5) {
        this.$toast.open({
          message: this.$t('file.exceed'),
          position: "is-top",
          type: "is-warning"
        });
        return;
      }
      if(Object.keys(selectedGroupObj).length === 0) {
        // this.$store.commit('SET_MEMBERSRANGE', {});
        // this.$emit('selectedGroupMembers', {});
        this.$emit('selectedMemberList', {});
      } 
      // if (JSON.stringify(this.groupMembersListObj) !== JSON.stringify(this.members_range)) {this.membersinfo.member
       if (JSON.stringify(this.groupMembersListObj) !== JSON.stringify(this.members_range)) {
         //this.$store.commit('SET_MEMBERSRANGE', this.groupMembersListObj);
         this.$emit('selectedMemberList', this.groupMembersListObj);
      }
       this.$emit("closeAllCard");
    }
  }
};
</script>
<style lang="scss" scoped>
.filter-modal {
  /deep/.modal-background{
    background-color: rgba(10, 10, 10, 0.56);
  }
  .header-input-box {
    padding: 15px 5px 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #dbdbdb;
    .header-search-input {
      width: 90%;
    }
    .search-select{
      font-size: 14px;
      text-align: center;
      padding: 10px 0px;
      color: #000;
    }
  }
  .members{
    width: 100%;
    height: 100%;
    overflow: auto;
    padding: 15px;
    .members-list-item {
      position: relative;
      line-height: 40px;
      font-size: 14px;
      .checkbox-right {
        position: absolute;
        top: 12px;
        right: 0px;
        cursor: pointer;
      }
      .group-remark {
        line-height: 1;
        width:210px;
        padding-left: 10px;
        font-size: 14px;
        overflow: hidden;
        text-overflow: ellipsis;
        -o-text-overflow: ellipsis;
        white-space: nowrap;
        display: inline-block;
        position: relative;
        top: 2px;
      }
    }
    .members-list-item:not(:last-child) {
      margin:0px 0px 20px 0px;
    }
  }
  .operation {
    display: flex;
    justify-content: flex-end;
    padding: 10px 10px;
    box-shadow: 0 -2px 3px -1px rgba($color: #000000, $alpha: 0.2);
    box-sizing: border-box;
    .operation-button {
      margin-left: 5px;
    }
  }
}
.warning-modal{
  width: 100%;
  height: 100%;
  background-color:rgba(0,0,0,0.3) ;
  position: absolute;
  z-index: 5;
  .alert-beyond{
    width: 317px;
    height: 123px;
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    margin: auto;
    z-index: 6;
    .alert-header{
      text-align: left;
      padding: 8px 0px;
    }
    .alert-footer{
      float: right;
    }
  }
}
</style>