const filters = {
  timeFilter: function(val, format='yyyy-MM-dd') {
    var date = new Date(val);
    if (/(y+)/.test(format)) {
      format = format.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
    }
    let o = {
      'M+': date.getMonth() + 1,
      'd+': date.getDate(),
      'h+': date.getHours(),
      'm+': date.getMinutes(),
      's+': date.getSeconds()
    };
    for(let k in o){
      if (new RegExp(`(${k})`).test(format)) {
        let str = o[k] + '';
        format = format.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : ('00' + str).substr(str.length));
      }
    }
    return format;
  },
  sizeFilter: function(value) {
    if (value === null || value === '' || value === 0) {
      return '0B';
    }
    let unitArr = new Array("B","KB","MB","GB","TB","PB","EB","ZB","YB");
    let index = 0;
    let srcsize = parseFloat(value);
    index = Math.floor(Math.log(srcsize)/Math.log(1024));
    let size = srcsize/Math.pow(1024,index);
    size = size.toFixed(1);//保留的小数位数
    return size + unitArr[index];
  }
}

export default filters;