<template>
  <div id="app">
    <search-manager></search-manager>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import SearchManager from './components/Search/index';
export default {
  name: 'app',
  components: {
    SearchManager
  },
  computed: {
    ...mapGetters([ 'uploader_list' ])
  },
  created() {
    console.log('get imsdkservice: ', this)
    console.log('i get env: ', process.env.NODE_ENV);
    if (process.env.NODE_ENV === 'development') {
      let loginParam = {
        user: '008618792550841', // 用户名 **手机号要加 0086**
        pwd: '123123', // 密码
        userType: 1, // 用户类型,不修改
        server: 'vrv', // 服务器,不修改
        nation: '0086' // 国家,不修改
      };
      this.$Service.auth.login(loginParam, resp => {
        console.log('i get login result: ', resp);
        // 0 成功 113 账号已登录
        if (resp.code === 0 || resp.code === 113) {
          // 登录成功 跳转路由
          this.init(); // 初始化数据
        }
      })
    }
    this.init(); // 初始化数据
  },
  methods: {
    init() {
      // 获取文件列表数据
      const param = {
        targetId: '0',
        count: 1000
      }
      this.$Service.file.getFileList(param, resp => {
        console.log('getFileList：resp=>>>>>>>>>', resp);
        if (resp.code === 0) {
          if (resp.fileInfos.length === 0) {
            return;
          }
          this.$store.commit('SET_FILELIST', resp.fileInfos);
        }
      })
      this.getFilePath();
      this.getAccountInfo();
    },
    // 获取文件目录地址
    getFilePath() {
      this.$Service.config.userFilePath(null, resp => {
        console.log(`user_filePath:::`, resp);
        this.$store.commit('SET_FILE_PATH', resp.path);
      });
    },
    // 获取账户信息
    getAccountInfo() {
      this.$Service.user.getAccountInfo(null, resp => {
        // this.$Service.log(`获取账户信息回调${JSON.stringify(resp)}`);
        this.$store.commit('SET_ACCOUNT', resp);
      })
    },
  }
}
</script>

<style lang="scss">
body,
html,
#app {
  width: 100%;
  height: 100%;
  font-family: '微软雅黑', 'Microsoft YaHei', Arial, Helvetica, sans-serif, '宋体';
  overflow: hidden;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  background: transparent;
  position: relative;
}
* {
  &::-webkit-scrollbar {
    width : 6px;  /*高宽分别对应横竖滚动条的尺寸*/
  }
  &::-webkit-scrollbar-thumb {
    height: 5px;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    background: #C0C4CC;
  }
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    background: #ededed;
  }
}
@import '~bulma/css/bulma.css';
</style>
