<template>
  <div class="file-sort" :class="select">
    <i class="top-arrow arrow" @click.stop="sort(1)" :title="$t('file.asc')"></i>
    <i class="bottom-arrow arrow" @click.stop="sort(0)" :title="$t('file.desc')"></i>
  </div>
</template>

<script>
export default {
  name: 'file-sort',
  components: {},
  props: {
    type: Number,
    sorting: Number,
    sortRule: Number
  },
  computed: {
    select() {
      return [
        {
          'asc-sort': this.sortRule === 1 && this.sorting === this.type,
          'desc-sort': this.sortRule === 0 && this.sorting === this.type
        }
      ]
    }
  },
  methods: {
    sort(rule) {
      this.$emit('sort', rule, this.type);
    }
  }
}

</script>
<style lang='scss' scoped>
.file-sort{
  height: 100%;
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  .arrow{
    border: 6px solid #999;
    border-left-color: transparent;
    border-right-color: transparent;
    cursor: pointer;
    transition: .3s;
    &:hover{
      transform: scale(1.3); 
    }
  }
  .top-arrow{
    border-top-color: transparent;
    position: relative;
    bottom: 2px;
  }
  .bottom-arrow{
    border-bottom-color: transparent;
    position: relative;
    top: 2px;
  }
  &.asc-sort .top-arrow{
    border-bottom-color: #000;
  }
  &.desc-sort .bottom-arrow{
    border-top-color: #000;
  }
}
@media screen and (max-width: 770px){
  .file-sort{
    margin-left: 5px; 
  }
}
</style>
