<template>
  <div v-if="isShow" class="bubble" id="searchBubble" v-click-outside="close">
    <d-card shadow="hover" :body-style="{ padding: '5px 10px' }">
      <div class="range-item">
        <span class="text" @click.stop.self="showContactOrGroup">{{`${$t('file.range')}：`}}</span>
        <div class="show-add-list" @click.stop="showContactOrGroup">
          <span v-if='objectValue === undefined' class="placeHolder" >{{$t('file.searchPlaceholder')}}</span>
          <d-card class="member" :body-style="{ padding: '2px' }"  v-for="(element,index) in getShowRange" :key="index" shadow="hover">
            <d-avatar class="member-avatar" size='16' :src="element.thumbAvatar || element.avatar"></d-avatar>
            <span class="member-name">{{element.name || element.remark || element.groupName}}</span>
            <d-icon icon="d-icon-circle-close" size="15" color="#666" class="delete-icon" @click.stop="deleteMember(element.id || element.groupId)"></d-icon>
          </d-card>
        </div>
      </div>
      <div class="opeation">
        <d-button size="mini" type="info" ripple @click="confirm" >{{ $t('file.confirm')}}</d-button>
      </div>
    </d-card>
    <file-filter-members v-if="showFilterMembers" @close='showFilterMembers = false' @selectedMembersRange="selectedMembersRange" :returnValue='this.selectedMembersRangeObj'></file-filter-members>
  </div>
</template>
<script>
import FileFilterMembers from './FileFilterMembers';
import ClickOutside from '../../../doui/utils/clickoutside.js';
import { mapGetters } from 'vuex';
export default {
  name: 'file-scope-filter',
  components: {
    FileFilterMembers
  },
  directives: { ClickOutside },
  props: {
    isShow: { 
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      showFilterMembers: false,
      selectedMembersRangeObj: {},
      objectValue:[]
    };
  },
  computed: {
    ...mapGetters(['members_range']),
    getShowRange() {
      //console.log('get show member', this.members_range);
      // let key = Object.keys(this.members_range);
      // return this.members_range[key];
      console.log('get show member', this.selectedMembersRangeObj);
      let key = Object.keys(this.selectedMembersRangeObj);
      this.objectValue = Object.values(this.selectedMembersRangeObj)[0];
      return this.selectedMembersRangeObj[key];
    },
    // cbGetShowRange() {
    //   console.log('cbGetShowRange',this.members_range)
    //    return this.members_range;
    // }
  },
  methods: {
    showContactOrGroup() {// 点击显示联系人或者群列表
      this.showFilterMembers = true;
      // if((this.showAddMember.contact ) || (this.showAddMember.group) != undefined ){
      //   console.log('点击后查看当前数据',this.showAddMember.contact)
      //   console.log(Object.values(this.showAddMember.contact)) 
      // }
      
    },
    close() {// 点击外部关闭
      this.$emit('close')
    },
    deleteMember(id) { //根据ID点击删除
      if(Object.keys(this.selectedMembersRangeObj) == 'contact'){
         this.$delete(this.selectedMembersRangeObj.contact, id);
         let valueArr = Object.keys(this.selectedMembersRangeObj.contact)
          if(valueArr.length === 0){
            this.$delete(this.selectedMembersRangeObj, 'contact');
            this.$store.commit('SET_MEMBERSRANGE', this.selectedMembersRangeObj);
            this.close()
        }
      }else if(Object.keys(this.selectedMembersRangeObj) == 'group'){
        this.$delete(this.selectedMembersRangeObj.group, id);
        let valueArr = Object.keys(this.selectedMembersRangeObj.group)
          if(valueArr.length === 0){
            this.$delete(this.selectedMembersRangeObj, 'group');
            this.$store.commit('SET_MEMBERSRANGE', this.selectedMembersRangeObj);
            this.close()
        }
      }else if(Object.keys(this.selectedMembersRangeObj) == 'member'){
        this.$delete(this.selectedMembersRangeObj.member, id);      
        let valueArr = Object.keys(this.selectedMembersRangeObj.member)
          if(valueArr.length === 0){
            this.$delete(this.selectedMembersRangeObj, 'member');
            this.$store.commit('SET_MEMBERSRANGE', this.selectedMembersRangeObj);
            this.close()
        }
      }
    },
    // 确认筛选信息
    confirm() {
      this.$store.commit('SET_MEMBERSRANGE', this.selectedMembersRangeObj);
      this.close()
    },
    selectedMembersRange(val) {
      this.selectedMembersRangeObj = Object.assign({}, val);
    }
  }
};
</script>
<style lang="scss">
.bubble {
  position: absolute;
  top: 70px;
  right: 40px;
  flex-wrap: nowrap;
  z-index: 2;
  &:before {
    content: "";
    height: 0px;
    width: 0px;
    border: 10px solid #e0e0e0;
    position: absolute;
    top: -20px;
    right: 10%;
    border-top-color: transparent;
    border-left-color: transparent;
    border-right-color: transparent;
  }
  &:after {
    content: "";
    height: 0px;
    width: 0px;
    border: 10px solid #fff;
    position: absolute;
    top: -19px;
    right: 10%;
    border-top-color: transparent;
    border-left-color: transparent;
    border-right-color: transparent;
  }
  .range-item {
    display: flex;
    flex: 1;
    align-items: center;
    position: relative;
    cursor: pointer;
    padding: 10px 0px;
    .text {
      white-space: nowrap;
      font-size: 15px;
      line-height: 33px;
    }
    .show-add-list {
      min-height: 33px;
      display: flex;
      flex-wrap: wrap;
      min-width: 190px;
      max-width: 271px;
      border-bottom:1px solid #2196F3;
      align-self: flex-end;
      box-sizing: border-box;
      font-size: 14px;
      .placeHolder{
        line-height: 2;
        color: #999;
        position: relative;
        left: 30px;
      }
      .member {
        margin-right: 4px;
        margin-bottom: 4px;
        padding: 0 2px;
        &>.d-card__body {
          display: flex;
          align-items: center;
        }
        // /deep/ .d-card .d-card__body {
        //   width: 500px;
        // }
        .member-name {
          display: inline-block;
          max-width: 50px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          margin: 0 4px;
        }
      }
      .show-add-list:empty:before{
			  content:attr(placeholder);
        color: #2196F3
      }
    }
  }
  .opeation{
    display: flex;
    justify-content: flex-end;
  }
}
</style>









