<template>
  <div class="file-list-head">
    <div class="head-item" v-for="(item, index) in table" :key="index" :style="getHeadItemStyle(item)" :title="item.name">
      <span class="head-name">{{ item.name }}</span>
      <file-sort v-if="item.index === 3 || item.index === 4" :type="item.index" :sorting="sorting" @sort="sort" :sortRule="sortRule" :key="index"></file-sort>
    </div>
  </div>
</template>

<script>
import FileListMixins from './FileListMixin';
import FileSort from './FileSort';
export default {
  name: 'file-list-head',
  mixins: [ FileListMixins ],
  components: {
    FileSort
  },
  props: {
    sorting: Number,
    sortRule: Number
  },
  data() {
    return {
      // sorting: 4, // 排序项的index
      // sortRule: 0 // 排序规则 1升0降-1初始化
    };
  },
  methods: {
    getHeadItemStyle(item) {
      return {
        'width': `${item.width}%`,
        'justify-content': item.align
      }
    },
    /** 
     * 排序函数
     * rule 排序规则 1升序 0降序 -1初始化
     * type 排序类型 自定义字段
     */
    sort(rule, type) {
      // 当排序规则无变化时 return掉
      if (this.sorting === type && this.sortRule === rule) {
        return;
      }
      this.$emit('sort', rule, type);
    }
  }
}

</script>
<style lang='scss' scoped>
.file-list-head{
  display: flex;
  background: #fff;
  border-radius: 4px;
  padding: 0px 5px;
  box-sizing: border-box;
  color: #6f6f6f;
  font-size: 14px;
  .head-item{
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    .head-name {
      max-width: 100%;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
    }
  }
}
</style>
