<template>
  <div class="item-content">
    <div class="item-img" :style="getItemStyle(0)">
      <d-avatar shape="square" :src="getFileImg(file.name)"></d-avatar>
    </div>
    <p class="item item-name" :style="getItemStyle(1)" :title="file.name || $t('file.unknowFile')">{{ file.name || $t('file.unknowFile') }}</p>
    <p class="item item-from" :style="getItemStyle(2)" :title="getUploaderName">{{ getUploaderName }}</p>
    <p class="item item-size" :style="getItemStyle(3)" :title="file.size | sizeFilter">{{ file.size | sizeFilter }}</p>
    <p class="item item-time" :style="getItemStyle(4)" :title="file.time | timeFilter">{{ file.time | timeFilter }}</p>
    <div class="item-operation" :style="getItemStyle(5)">
      <div class="item-open">
        <d-button size="mini" :title="$t('file.open')" ripple @click="openFile(file)">{{ $t('file.open') }}</d-button>
      </div>
      <div class="item-open-folder">
        <d-button size="mini" :title="$t('file.openfolder')" ripple @click="openFolder(file)">{{ $t('file.openfolder') }}</d-button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import FileListMixin from './FileListMixin';
import filters from 'utils/filters.js';
import FileImg from 'utils/fileImg';
export default {
  name: 'file-list-item',
  mixins: [ FileListMixin ],
  components: {},
  filters: { 
    timeFilter: filters.timeFilter,
    sizeFilter: filters.sizeFilter
  },
  props: {
    file: Object
  },
  data() {
    return {
    };
  },
  computed: {
    ...mapGetters([ 'uploader_list', 'user_account' ]),
    getUploaderName() {
      return this.file.uploader ? (this.file.uploader === this.user_account.name ? this.$t('file.me') : this.file.uploader) : this.$t('file.getting');
    }
  },
  mounted() {
    this.getUserInfo();
  },
  updated() {
    this.getUserInfo();
  },
  methods: {
    getItemStyle(index) {
      return {
        'width': `${this.table[index].width}%`,
        'text-align': this.table[index].align
      }
    },
    getFileImg(name) {
      let suffix = name.substring(name.lastIndexOf('.') + 1);
      if (FileImg.hasOwnProperty(suffix)) {
        return FileImg[suffix];
      }
      return FileImg.default;
    },
    getUserInfo() {
      // 如果有值,则不调接口直接赋值
      if (this.uploader_list[this.file.userid]) {
        console.log('已存在该用户信息');
        this.$set(this.file, 'uploader', this.uploader_list[this.file.userid]);
        return;
      }
      // 未存储过直接调接口获取
      this.$Service.search.getUserInfo({ userId: this.file.userid }, resp => {
        console.log('获取用户信息resp=>>>>>>', resp);
        if (resp.code === 0) {
          let name = resp.users.name ? resp.users.name : this.$t('file.unknowUser');
          this.$set(this.file, 'uploader', name);
          console.log('获取后的item', this.file);
          this.$store.commit('SET_UPLOADERLIST', { userId: this.file.userid, name})
        } else {
          this.$set(this.file, 'uploader', this.$t('file.unknowUser'));
        }
      });
    },
    openFile(file) {
      console.log('file =>>>>>>', file);
      // 解密流程
      this.isNeedDecryptFile(file).then(result => {
        if (result === 1 || result === 2 || result === 3) { // 错误处理
          let message = result === 1 || result === 2 ? this.$t('file.openFileFailed') : this.$t('file.missingFiles');
          this.$toast.open({
            message,
            position: "is-top",
            type: "is-danger"
          });
        } else {
          this.openFileOrDir({path: result, operType: 1}).then(result => {
            if (!result.result) {
              this.$toast.open({
                message: this.$t('file.missingFiles'),
                position: "is-top",
                type: "is-danger"
              });
            }
          })  
        }
      })
    },
    openFolder(file) {
      if (file.encrypt_key === '') {
        // 不需要解密直接打开 通过shell脚本打开
        let isSuccess = this.$shell.showItemInFolder(file.path);
        console.log('打开文件夹是否成功', isSuccess);
        if (!isSuccess) {
          this.$toast.open({
            message: this.$t('file.missingFiles'),
            position: "is-top",
            type: "is-danger"
          });
        }
        return
      }
      // 解密流程
      this.isNeedDecryptFile(file).then(result => {
        if (result === 1 || result === 2 || result === 3) { // 错误处理
          let message = result === 1 || result === 2 ? this.$t('file.openFileFailed') : this.$t('file.missingFiles');
          this.$toast.open({
            message,
            position: "is-top",
            type: "is-danger"
          });
        } else {
          let isSuccess = this.$shell.showItemInFolder(result);
          console.log('打开文件夹是否成功', isSuccess);
           if (!isSuccess) {
            this.$toast.open({
              message: this.$t('file.missingFiles'),
              position: "is-top",
              type: "is-danger"
            });
          }
        }
      })
    }
  }
}

</script>
<style lang='scss' scoped>
.item-content{
  display: flex;
  width: 100%;
  font-size: 12px;
  color: #999;
  align-items: center;
  .item{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: 5px 0px;
    box-sizing: border-box;
  }
  .item-img{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .item-name{
    padding-right: 10px;
  }
  .item-operation{
    display: flex;
    align-items: center;
    padding: 5px;
    .item-open{
      flex-grow: 1;
      flex-basis: 0%;
      display: flex;
      justify-content: center;
      padding: 0 5px;
      box-sizing: border-box;
    }
    .item-open-folder{
      flex-grow: 1;
      flex-basis: 0%;
      padding: 0 5px;
      display: flex;
      box-sizing: border-box;
    }
  }
}
@media screen and (max-width: 995px){
  .item-operation .d-button{
    padding: 0 10px;
  }
}
@media screen and (max-width: 760px){
  .item-content .item-operation .item-open-folder .d-button{
    padding: 0px 5px;
    box-sizing: border-box;
    width: 80%;
    /deep/ span{
      width: 100%;
      display: inline-block;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}
@media screen and (max-width: 680px){
  .item-content{
    .item-operation{
      .item-open{
        flex-grow: 1!important;
        .d-button{
          padding: 0 25px;
        }
      }
      .item-open-folder{
        display: none;
      }
    }
  }
}
</style>
