<template>
    <transition
        name="slide-fade">
        <div class="toast" v-show="isActive" :class="type">
            <!-- <d-icon :icon="getIcon"></d-icon> -->
            <div v-html="message"></div>
        </div>
    </transition>
</template>

<script>
export default {
  props: {
    type: {
      type: String,
      default: 'is-dark'
    },
    message: String,
    duration: {
      type: Number,
      default: 2000,
    },
    position: {
      type: String,
      default: 'is-top',
      validator(value) {
        return (
          [
            'is-top-right',
            'is-top',
            'is-top-left',
            'is-bottom-right',
            'is-bottom',
            'is-bottom-left'
          ].indexOf(value) > -1
        );
      }
    },
    container: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      isActive: false,
      parent: null,
      newContainer: this.container,
      newDuration: this.duration
    };
  },
  computed: {
    getIcon(){
      switch (this.type) {
        case 'is-success':
          return 'd-icon-circle-complete';
        case 'is-info':
          return 'd-icon-circle-about';
        case 'is-warning':
          return 'd-icon-circle-warning';
        case 'is-error':
          return 'd-icon-circle-close';
        case 'is-danger':
          return 'd-icon-triangle-warning';

      }
    },
  },
  methods: {
    insertEl() {
      this.parent.className = '';
      this.parent.classList.add('notices', this.position);
      this.parent.appendChild(this.$el);
    },
    hasChild(parent) {
      return parent !== null && parent.childElementCount > 0;
    },
    close() {
      clearTimeout(this.timer);
      this.isActive = false;
      // Timeout for the animation complete before destroying
      setTimeout(() => {
        this.$destroy();
        if (typeof this.$el.remove !== 'undefined') {
          this.$el.remove();
        } else {
          this.$el.parentNode.removeChild(this.$el);
        }
      }, 150);
    },
    show() {
      if (this.hasChild(this.parent)) {
        // Add to "queue" (recursive) if already has a notice
        setTimeout(() => this.show(), 250);
        return;
      }
      // This is from Toast or Snackbar
      this.insertEl();
      this.isActive = true;
      this.timer = setTimeout(() => this.close(), this.newDuration);
    },
    init() {
      let parent;
      parent = document.querySelector('.notices');

      const container =
        document.querySelector(this.newContainer) !== null
          ? document.querySelector(this.newContainer)
          : document.body;

      if (!parent) parent = document.createElement('div');

      this.parent = parent;
      if (this.newContainer) parent.style.position = 'absolute';

      container.appendChild(parent);
    }
  },
  beforeMount() {
    this.init();
  },
  mounted() {
    this.show();
  }
};
</script>
<style lang="scss" scoped>
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to {
  transform: translateY(15px);
  opacity: 0;
}
.notices {
  .toast {
    border-radius: 0px;
    padding: 0.55em;
    width: 70%;
    min-width: 480px;
    border-top-width: 3px;
    border-top-color: rgba(0,0,0,0.3);
    border-top-style: inset;
    &>div{
      margin: 0 auto;
    }
    .icon{
      margin-right: 8px;
      font-size: 14px;
      color:rgba(0,0,0,0.3);
    }
  }
  .toast.is-success{
    font-size: 14px;
    background: rgb(0,176,88);
    color: #fff;
  }
  .toast.is-info{
    font-size: 14px;
    background: rgb(0,151,244);
    color: #fff;
  }
  .toast.is-warning{
    font-size: 14px;
    background: rgb(255,133,0);
    color: #fff;
  }
  .toast.is-danger{
    font-size: 14px;
    background: rgb(255,66,67);
    color: #fff;
  }
  .toast.is-error{
    font-size: 14px;
    background: rgb(255,66,67);
    color: #fff;
  }
}
</style>