<!-- 联系人/群/会话列表 搜索范围过滤 -->
<template>
  <v-modal class="filter-modal" @close="cancelScopeFilter">
    <div class="header-input-box" slot="header-top">
      <d-input
        class="header-search-input"
        v-model="searchInfo"
        :placeholder="$t('file.searchContactsGroups')"
        prefixIcon="d-icon-search"
        outlined
        size="small"
        clearable
      ></d-input>
    </div>
    <div class="tpye-tab">
      <v-tabs position="is-centered" v-model="currentTab"  @change="changeTab">
        <v-tab-item v-for="(tabs, index) in tabList" :label="tabs.name" :key="index">
          <div class="tab-component">
            <component v-show="currentTab === index" :is="tabs.tabView" :searchInfo="searchInfo" :returnPageValue='returnValue' @selectMembers="selectMembers" @selectedMemberList='selectedMemberList' @closeBothCard='cancelScopeFilter'></component>
          </div>
        </v-tab-item>
      </v-tabs>
    </div>
    <div class="operation" slot="footer-bottom">
      <d-button class="operation-button" size="mini" @click="confirmAdd">{{$t('file.confirm')}}</d-button>
      <d-button class="operation-button" size="mini" type="danger" @click="cancelScopeFilter">{{$t('file.cancel')}}</d-button>
    </div>
  </v-modal>
</template>

<script>
import GroupMembersList from "../../Group/GroupMembersList";
import { mapGetters } from 'vuex';
export default {
  props:['returnValue'],
  data() {
    return {
      tabList: [ 
        {
          name: this.$t('file.contact'),
          tabView: resolve => require(['../../Contact'], resolve)
        }, 
        {
          name: this.$t('file.group'),
          tabView: resolve => require(['../../Group'], resolve)
        }
      ],
      contactsAvatar: "",
      groupAvatar: "",
      groupId: "",
      selectedMembers: {},
      currentTab: 0,
      selectedRange: {}, // 选择的范围
      searchInfo:''
    };
  },
  components: {
    GroupMembersList
  },
  mounted() {
    this.getCurrentTab();
  },
  computed: {
    ...mapGetters([ 'members_range' ]),
    toContactValue() {
      // if(this.currentTab = 0){
        console.log('in filefilter', this.showAddMember)
        return this.showAddMember
      // }
    },

  },
  methods: {
    cancelScopeFilter() {
      this.$emit("close");
    },
    confirmAdd() {
      let selectedObj = (() => {
        for(var key in this.selectedRange){
          return this.selectedRange[key];
        }
      })();
      if (Object.keys(selectedObj).length > 5) {
        this.$toast.open({
          message: this.$t('file.exceed'),
          position: "is-top",
          type: "is-warning"
        });
        return;
      }
      if (Object.keys(selectedObj).length === 0) { //传值为空
        // this.$store.commit('SET_MEMBERSRANGE', {});
        this.$emit('selectedMembersRange', {});
      } else if (JSON.stringify(this.selectedRange) !== JSON.stringify(this.members_range)){
        //this.$store.commit('SET_MEMBERSRANGE', this.selectedRange);
        this.$emit('selectedMembersRange', this.selectedRange);
      }
      this.$emit("close");
    },
    /**
     * 切换tab
     */
    changeTab(value) {
      this.currentTab = value;
      this.searchInfo = ''; 
      this.$emit('changeTab', value)
    },
    // 获取当前tab
    getCurrentTab() {
      // let tabName = this.members_range && Object.keys(this.members_range)[0];
      let tabName = this.returnValue && Object.keys(this.returnValue)[0];
      let tabObject = {
        'contact': 0,
        'group': 1,
        'member': 1
      };
      this.currentTab = tabObject[tabName] ? tabObject[tabName] : 0;
    },
    selectMembers(val) {
      this.selectedRange = Object.assign({}, val);
    },
    selectedMemberList(selectGroupMembers) {
      this.$emit('selectedMembersRange',selectGroupMembers)
    }
  }
}
</script>

<style lang="scss">
.filter-modal {
  /deep/ .modal-background{
    background-color: rgba(10, 10, 10, 0.66);
  }
  .header-input-box {
    padding: 15px 5px 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    .header-search-input {
      width: 90%;
    }
  }
  
  /deep/ .modal-card {
    background-color: #fff;
    width: 327px;
    height: 90%;
    border-radius: 4px;
  }
  .tpye-tab {
    height: 100%;
    /deep/ .b-tabs {
      height: 100%;
      display: flex;
      flex-direction: column;
      .tabs{
        font-size: 14px;
        flex-shrink: 0;
        margin-bottom: 0px;
      }
      .tab-content {
        flex: 1;
        overflow: hidden;
        .tab-item {
          height: 100%;
          .tab-component {
            height: 100%;
            overflow: hidden;
          }
        }
      }
    }
  }
  .operation {
    display: flex;
    justify-content: flex-end;
    padding: 10px 10px;
    // border-top: 1px solid #999;
    box-shadow: 0 -2px 3px -1px rgba($color: #000000, $alpha: 0.2);
    box-sizing: border-box;
    .operation-button {
      margin-left: 5px;
    }
  }
}
</style>