import { mapGetters } from 'vuex';

const mixin = {
  data() {
    return {
      table: [
        { index: 0, name: '', label: '图标', width: 7, align: 'left' },
        { index: 1, name: this.$t('file.name'), label: '名称', width: 28, align: 'left' },
        { index: 2, name: this.$t('file.source'), label: '来源', width: 10, align: 'left' },
        { index: 3, name: this.$t('file.size'), label: '大小', width: 15, align: 'center' },
        { index: 4, name: this.$t('file.time'), label: '时间', width: 15, align: 'center' },
        { index: 5, name: this.$t('file.operation'), label: '操作', width: 25, align: 'center' },
      ],
      sortTypeObject: {
        3: 'size',
        4: 'time'
      },
      sortRuleObject: {
        1: 'asc',
        0: 'desc'
      },
      decryptFilePath: String // 解密文件位置
    }
  },
  computed: {
    ...mapGetters([ 'user_filePath' ])
  },
  methods: {
    /**
     * 判断滚动条滚动到底部
     * @param {*} el - 绑定的dom元素
     * @returns - 是/否
     */
    isScrollToBottom(el) {
      let [scrollTop, offsetHeight, scrollHeight] = [el.scrollTop, el.offsetHeight, el.scrollHeight];
      return !(scrollTop + offsetHeight < scrollHeight)
    },
    /**
     * openFileOrDir 打开文件或者打开文件位置 文件夹osx暂不可用
     * @param {String} path  文件路径
     * @param {Number} operType  操作类型 1 打开文件 2 打开文件位置
     */
    openFileOrDir(arg) {
      return new Promise(resolve => {
        let fileOperateParam = {
          filepath:  !this.$isOSX ? arg.path : arg.path.replace(/\(/ig, '\\(').replace(/\)/ig, '\\)'), //路径
          type: 7, // 操作类型 0：判断文件或文件夹是否存在 1.创建文件夹 2.删除文件 3.重命名文件 4.查看大小 6.异步删除 7.打开文件或文件夹
          operType: arg.operType
        }
        console.log('fileOperateParam', fileOperateParam)
        this.$Service.file.fileOperate(fileOperateParam, openRes => {
          console.log('open file result =>>>>>', openRes);
          resolve(openRes)
        })
      }) 
    },
    /**
     * 判断是否需要解密文件
     */
    isNeedDecryptFile(file) {
      return new Promise((resolve) => {
        // 当文件路径 为默认路径时 则需要解密
        if (file.path === this.user_filePath + this.getFileName(file.url)) {
          if (!file.encrypt_key) {
            // 无密钥时的处理
            if (this.$Service.file.fileOperate({ type: 0, filepath: file.path})) {
              this.decryptFilePath = this.createCachePath(file.name);
              if ( decryptFilePath === 1) {
                resolve(1);
                return;
              }
              this.handleNoPasswordFile(file.path, this.decryptFilePath);
              resolve(this.decryptFilePath);
              return;
            }
            resolve(3);
            return;
          }
          // 解密过
          if (!this.$Service.file.fileOperate({ type: 0, filepath: this.decryptFilePath})) {
            this.decryptFile(file).then(result => {
              resolve(result);
            })
          } else {
            resolve(this.decryptFilePath);
          }
        } else {
          if (!this.$Service.file.fileOperate({ type: 0, filepath: file.path})) {
            resolve(3);
          } else {
            resolve(file.path);
          }
        }
      })
    },
    /**
     * 解密文件
     */
    decryptFile(file) {
      return new Promise((resolve) => {
        if (this.$Service.file.fileOperate({ type: 0, filepath: file.path})) {
          let decryptFilePath = this.createCachePath(file.name);
          if ( decryptFilePath === 1) {
            resolve(1);
            return;
          }
          this.decrypted(file.encrypt_key, file.path, decryptFilePath, 2).then(decryptedRes => {
            if (decryptedRes.result) {
              resolve(decryptFilePath);
            } else {
              resolve(2);
            }
          })
        } else {
          resolve(3);
        }
      })
    },
    /**
     * 创建缓存文件路径
     */
    createCachePath(fileName) {
      const cachePath = `${this.user_filePath}cache/`;
      let isCreateCache = this.$Service.file.fileOperate({
        filepath: cachePath,
        type: 1
      })
      console.log('cachePath', cachePath);
      console.log('isCreateCache', isCreateCache);
      if (isCreateCache) {
        console.log('创建缓存目录成功或已存在');
        return cachePath + fileName.replace(/\//g, '_');
      } else{
        console.log('创建缓存目录失败');
        return 1;
      }
    },
    /**
     * @param {String} encryptkey 解密密码
     * @param {String} srcpath 原图路径
     * @param {String} destpath 保存路径
     * @param {Number} flag 是否可读  1 可读可写  2只读 
     */
    decrypted(encryptkey, srcpath, destpath, flag = 1) {
      return new Promise((resolve, reject) => {
        let decryptFileParam = {
          encryptkey: encryptkey,
          srcpath: srcpath,
          destpath: destpath,
          flag: flag
        }
        console.log('decryptFileParam', decryptFileParam)
        this.$Service.file.decryptFile(decryptFileParam, resp => {
          console.log('decryptFileResult', resp);
          resolve(resp)
        })
      })
    },
     /**
     * 工具函数 获取文件名称(无后缀)
     */
    getFileName(str) {
      let index = str.lastIndexOf('/');
      let path = str.substr(index + 1); // 取文件完整名称
      let fileName = path.substr(0, path.lastIndexOf('.')); // 取文件名称(无后缀)
      return fileName || path; // 处理无后缀的文件名称
    },
      /**
     * 处理无密码文件
     * 手动复制到指定位置
     * @param {String} srcpath 源路径
     * @param {String} destpath 目的路径
     */
    handleNoPasswordFile(srcpath, destpath) {
      this.$fs.createReadStream(srcpath).pipe(this.$fs.createWriteStream(destpath));
    },
  }
};
export default mixin;