import Vue from 'vue';
import App from './App.vue';
import doui from './doui';
import vrvui from './ui';
import i18n from './assets/common/i18n';
import store from './store';
import './mock'; // mock数据

Vue.config.productionTip = false;

Vue.use(doui);
Vue.use(vrvui);

const agent = navigator.userAgent.toLowerCase();
// 判断mock环境
console.log('mock environment:', agent);
if (agent.indexOf('electron') !== -1) {
  // 配置electron下的环境变量
  let service = window.require('electron').remote.getGlobal('service');
  i18n.locale = service.config.getLanguage();
  Vue.prototype.$shell = window.require('electron').shell;
  Vue.prototype.$fs = window.require('fs');
}
// 判断是否为OSX系统
Vue.prototype.$isOSX = process.platform === 'darwin';

new Vue({
  i18n,
  store,
  render: h => h(App)
}).$mount('#app')
