import Vue from 'vue';

const fileManager= {
  state: {
    account: {},
    fileList: [],
    searchList: null,
    uploader: {},
    filePath: '',
    contactList: [],
    groupList: [],
    membersList: [],
    selectedMembersRange: {}
  },
  mutations: {
    // 设置文件列表数组
    SET_FILELIST: (state, data) => {
      state.fileList = [...data];
    },
    // 设置发送人
    SET_UPLOADERLIST: (state, data) => {
      Vue.set(state.uploader, data.userId, data.name);
    },
    // 设置用户文件目录
    SET_FILE_PATH: (state, data) => {
      state.filePath = data;
    },
    // 设置搜索文件列表
    SET_SEARCHLIST: (state, data) => {
      state.searchList = data === null ? null : [...data];
    },
    // 设置用户信息
    SET_ACCOUNT: (state, data) => {
      state.account = data;
    },
    // 设置联系人列表
    SET_CONTACTLIST: (state, data) => {
      state.contactList = [...data];
    },
    // 设置群组列表
    SET_GROUPLIST: (state, data) => {
      state.groupList = [...data];
    },
    // 设置群成员列表
    SET_MEMBERSLIST: (state, data) => {
      state.membersList = [...data];
    },
    // 设置成员范围
    SET_MEMBERSRANGE: (state, data) => {
      state.selectedMembersRange = Object.assign({}, data);
    }
  }
};

export default fileManager;
