export function converseFullPinYin(pinyin) {
  let pinyinType =  /^[a-zA-Z]/g;
  if(!pinyin||!pinyinType.test(pinyin.substr(0, 1)))
  return `|${pinyin}`;
  let newPinyin = pinyin.substr(0, 1).toUpperCase();
  if ((newPinyin < 'A') || (newPinyin > 'Z')) {
    pinyin = `|${pinyin}`;
  }
  return pinyin;
};