<!-- 顶部搜索栏：文件全局搜索 -->
<template>
  <div class="search-bar" >
    <div class="search-input">
      <d-input v-model="searchInput" :placeholder="$t('file.searchFilePlaceholder')" prefixIcon="d-icon-search" outlined clearable @change="searchFile" @clear="clearSearch">
        <d-button class="filter-button" size="medium" slot="append" @click="isShow = true">{{$t('file.advancedFilter')}}</d-button>
      </d-input>
    </div>
    <!-- 联系人/群/会话列表 搜索范围过滤 -->
    <file-scope-filter :isShow="isShow" @close="isShow = false" :showAddMember="addList" @toSearchDetailValue='toSearchDetailValue'></file-scope-filter>
  </div>
</template>

<script>
import FileScopeFilter from './FileScopeFilter.vue';
import _ from 'lodash';
import { mapGetters } from 'vuex';
export default {
  data() {
    return {
      searchInput: '',
      showSearch: false,
      showBubble: false,
      addList:[],
      isShow: false,
    }
  },
  components: {
    FileScopeFilter
  },
  computed: {
    ...mapGetters([ 'file_list', 'members_range' ]),
  },
  watch: {
    searchInput(val) {
      if (val === '') {
        this.clearSearch();
      }
    }
  },
  methods: {
    close() {
      this.isShow = !this.isShow;
    },
    /**
     * 搜索文件
     */
    searchFile: _.debounce(function() {
      console.log('search file name =>>>>>>', this.searchInput);
      if (this.searchInput.trim() !== '') {
        let searchFileList = [];
        for (let i = 0, length = this.file_list.length; i < length; i++) {
          if (this.file_list[i].name.indexOf(this.searchInput.trim()) >= 0) {
            searchFileList.push(this.file_list[i]);
          }
        }
        console.log('search result=>>', searchFileList);
        this.$store.commit('SET_SEARCHLIST', searchFileList);
      } else {
        this.$store.commit('SET_SEARCHLIST', null);
      }
    }, 200)
    ,
    /**
     * 清空搜索
     */
    clearSearch() {
      this.$store.commit('SET_SEARCHLIST', null);
    },
    toSearchDetailValue(val) {
        this.$emit('FilterValue',val)
        // let valueName = Object.values(val.contact)
        // let resultArr = []
        // valueName.forEach(element => {
        //   resultArr.push(element.name)
        // })
        // this.searchInput = resultArr
      //   console.log('SearchBar index页面 拿到的值',Object.values(val.contact) || Object.values(val.group) || Object.values(val. member))
    }
  }
}
</script>

<style lang="scss">
.search-bar {
  height: 65px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  .search-input {
    width: calc(100% - 80px);
    /deep/ .d-input-wrapper .d-input-box .d-input {
      font-size: 14px;
    }
    .filter-button {
      height: 100%;
    }
  }
}

</style>