<!-- 消息管理器 -->
<template>
  <div class="search-manager">
    <search-bar @FilterValue='FilterValue'></search-bar>
    <search-detail :getFilterValue='getFilterValue'></search-detail>
  </div>
</template>

<script>
import SearchBar from './SearchBar/index'
import SearchDetail from './SearchDetail/index'
export default {
  name: 'search-manager',
  components: {
    SearchBar,
    SearchDetail
  },
  data(){
    return {
      getFilterValue:{}
    }
  },
  methods:{
    FilterValue(val) {
      this.getFilterValue = val
      console.log('index 拿到的searchBar的值',val)
    },
  }
}
</script>

<style lang="scss">
.search-manager {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: #f5f5f5;
}
</style>